import React, { useState } from "react";
import styles from "../css/tripBudget.module.css";

const API_URL = process.env.REACT_APP_API_URL;

const BudgetModal = ({
    type = "add", // "add"  "edit"
    show,
    onClose,
    onSubmit,
    initialData = {},
    room_id,
    teamMembers
}) => {
    if (!show) return null;

    const isEdit = type === "edit";

    const handleSave = async () => {
    //  form
    const description = document.getElementById("budgetItem").value;
    const type = document.getElementById("budgetCategory").value;
    const member_id = document.getElementById("budgetPaidBy").value;
    const value = document.getElementById("budgetAmount").value;
    const DateTime = document.getElementById("budgetDateTime").value;
    const dateOnly = DateTime.split("T")[0];

    const data = { room_id, description, type, member_id, value, dateOnly };

    //   edit  expend_id 
    if (isEdit && initialData.expend_id) {
        data.expend_id = initialData.expend_id;
    }

    try {
        //   API 
        const url = isEdit
            ? `${API_URL}/editBudget`
            : `${API_URL}/addBudget`;

        const response = await fetch(url, {
            method: isEdit ? "PUT" : "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(data),
        });

        const resData = await response.json();
        console.log(":", resData);

        //  
        alert(isEdit ? "!" : "!");
        onClose();
        window.location.reload();
    } catch (err) {
        console.error(":", err);
    }
};



    return (
        <>
            {/* Blur background */}
            <div className={styles.overlayBlur} onClick={onClose}></div>
            <div
                className={styles.modal}
                style={{ display: show ? "flex" : "none" }}
            >
                <div className={styles.modalContent}>
                    <div className={styles.modalMain}>
                        <h2>{isEdit ? "" : ""}</h2>
                        <span className={styles.close} onClick={onClose}>
                            &times;
                        </span>
                    </div>

                    <form onSubmit={(e) => {
                        e.preventDefault(); //  page reload
                        handleSave();       // 
                        }}>

                        <label htmlFor="budgetItem"> *</label>
                        <input
                            type="text"
                            id="budgetItem"
                            name="budgetItem"
                            placeholder=""
                            defaultValue={initialData.description || ""} 
                            required
                        />

                        <div className={styles.modalSplit}>
                            <div className={styles.modalSplitItem}>
                                <label htmlFor="budgetCategory"> *</label>
                                <select
                                    id="budgetCategory"
                                    name="budgetCategory"
                                    defaultValue={initialData.type || ""} 
                                    required
                                >
                                    <option value="">--  --</option>
                                    <option value="drink"></option>
                                    <option value="food"></option>
                                    <option value="travel"></option>
                                    <option value="other"></option>
                                </select>
                            </div>

                            <div className={styles.modalSplitItem}>
                                <label htmlFor="budgetPaidBy"> *</label>
                                <select
                                    id="budgetPaidBy"
                                    name="budgetPaidBy"
                                    defaultValue={initialData.member_id || ""}
                                    required
                                >
                                    <option value="">--  --</option>
                                    {teamMembers.map((member) => (
                                        <option key={member.member_id} value={member.member_id}>{member.member_name}</option>
                                    ))}
                                </select>
                            </div>
                        </div>

                        <label htmlFor="budgetAmount"> *</label>
                        <input
                            type="number"
                            id="budgetAmount"
                            name="budgetAmount"
                            placeholder=""
                            defaultValue={initialData.value || ""}
                            required
                        />

                        <label htmlFor="budgetDateTime"> / *</label>
                        <input
                            type="datetime-local"
                            id="budgetDateTime"
                            name="budgetDateTime"
                            defaultValue={initialData.paydate || ""}
                            required
                        />

                        <div style={{ display: "flex", marginTop: "10px" }}>
                            <div style={{ marginLeft: "auto" }}>
                                <button type="submit" className={styles.btnSaveEdit}>
                                    {isEdit ? "" : ""}
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </>
    );
};

export default BudgetModal;
