import React, { useState, useEffect, useRef } from "react"; // <--  useEffect, useRef
import { useNavigate,useLocation } from "react-router-dom";
import styles from "../css/popup-editTripPlan.module.css";
import flatpickr from "flatpickr"; // <-- Import flatpickr
import "flatpickr/dist/flatpickr.min.css"; // <-- Import CSS  flatpickr

// 1.  Props : initialData, roomId
export default function EditTripModal({ isOpen, onClose, initialData, roomId }) {
  const navigate = useNavigate();
  // 2.  State 
  const [tripName, setTripName] = useState("");
  const [tripText, setTripText] = useState("");
  // const [tripRange, setTripRange] = useState(""); //  state 
  const [startDate, setStartDate] = useState(""); // State  YYYY-MM-DD
  const [endDate, setEndDate] = useState("");     // State  YYYY-MM-DD
  const [tripBudget, setTripBudget] = useState("");
  const [tripPicFile, setTripPicFile] = useState(null); // State  File object ()
  const [previewSrc, setPreviewSrc] = useState(null); // State  URL  (  preview )
  const flatpickrInstance = useRef(null); // Ref  instance  flatpickr
  const base_api = process.env.REACT_APP_API_URL;
  const location = useLocation();
  // 3.  useEffect  Populate  &  flatpickr  Modal 
  useEffect(() => {
    //  Modal  (isOpen = true)
    if (isOpen) {
      if (initialData) {
        // --- Populate  State ---
        setTripName(initialData.title || "");
        setTripText(initialData.description || "");
        //  null  string   input 
        setTripBudget(initialData.total_budget == null ? "" : initialData.total_budget);
        setPreviewSrc(initialData.image || null); //  ()
        setStartDate(initialData.start_date || "");
        setEndDate(initialData.end_date || "");
        setTripPicFile(null); //  ()

        // ---  flatpickr ---
        //  instance  () 
        if (flatpickrInstance.current) {
            flatpickrInstance.current.destroy();
        }

        //  instance   ref
        flatpickrInstance.current = flatpickr("#editTripRange", { // <--  ID  Modal 
          mode: "range",
          dateFormat: "Y-m-d",
          defaultDate: (initialData.start_date && initialData.end_date) ? [initialData.start_date, initialData.end_date] : [], // <--  ()
          minDate: "today",
          locale: { //  locale object 
            firstDayOfWeek: 1, // Monday
            weekdays: {
              shorthand: ["", "", "", "", "", "", ""],
              longhand: ["", "", "", "", "", "", ""],
            },
            months: {
              shorthand: ["..", "..", "..", "..", "..", "..", "..", "..", "..", "..", "..", ".."],
              longhand: ["", "", "", "", "", "", "", "", "", "", "", ""],
            },
            rangeSeparator: '  ',
          },
          onChange: function(selectedDates) { //  onChange  onClose  range mode
            if (selectedDates.length === 2) {
              const start = selectedDates[0];
              const end = selectedDates[1];
              setStartDate(start.toISOString().split("T")[0]); //  state YYYY-MM-DD
              setEndDate(end.toISOString().split("T")[0]);     //  state YYYY-MM-DD
            } else if (selectedDates.length === 0) {
              // 
              setStartDate("");
              setEndDate("");
            }
          }
        });

      } else {
        //  initialData ( )
        setTripName("");
        setTripText("");
        setTripBudget("");
        setPreviewSrc(null);
        setStartDate("");
        setEndDate("");
        setTripPicFile(null);
        if (flatpickrInstance.current) {
            flatpickrInstance.current.destroy(); //  instance 
            flatpickrInstance.current = null;
        }
      }
    } else {
        //  Modal :  flatpickr instance
        if (flatpickrInstance.current) {
            flatpickrInstance.current.destroy();
            flatpickrInstance.current = null;
        }
    }

    // Cleanup function:  flatpickr instance  component unmount
    return () => {
        if (flatpickrInstance.current) {
            flatpickrInstance.current.destroy();
            flatpickrInstance.current = null;
        }
    };

  }, [isOpen, initialData]); //  Effect  Modal / 


  const handleImageChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      setTripPicFile(file); //  File object  ( S3)
      const reader = new FileReader();
      reader.onload = (e) => setPreviewSrc(e.target.result); //  preview 
      reader.readAsDataURL(file);
    } else {
      //  cancel,  ()  null
      setTripPicFile(null);
      setPreviewSrc(initialData?.image || null);
    }
  };

  // 4.  handleSubmit
  const handleSubmit = async (e) => {
    e.preventDefault();

    // ----- TODO: Logic Upload  ( tripPicFile) -----
    // 1.  tripPicFile  null:
    // 2.    tripPicFile  S3 ( UserPage.jsx  TripMainPage.jsx)
    // 3.    URL  S3  imageUrlForUpdate
    // 4.  tripPicFile  null:
    // 5.    previewSrc ( URL )  imageUrlForUpdate
    // -----  TODO -----
    let imageUrlForUpdate = previewSrc; // <--  URL  ()

    // 
    if (!startDate || !endDate) {
        alert("");
        return; // 
    }


    const updateData = {
      title: tripName,
      description: tripText,
      //  budget  null  string 
      total_budget: tripBudget === '' ? null : tripBudget,
      start_date: startDate,
      end_date: endDate,
      image: imageUrlForUpdate // <--  URL  S3 
    };

    try {
      const response = await fetch(`${base_api}/editTrip/${roomId}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(updateData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to update trip');
      }

      alert("!");
      onClose(); //  Modal
      const targetPath = `/tripPlan?room_id=${roomId}`;

   // 2.  ""  "" 
   if (location.pathname + location.search === targetPath) {
    
    // 3.  ( /tripPlan ) ->  F5
    window.location.reload();

   } else {
    
    // 4.  (  /tripBudget) -> 
    navigate(targetPath);
   }
      // navigate(`/tripPlan?room_id=${roomId}`);// <-- 

    } catch (err) {
      console.error(" Error updating trip:", err);
      alert(`: ${err.message}`);
    }
  };


  if (!isOpen) return null; //  Render  Modal 

  return (
    <>
      {/* Overlay  */}
      <div className={styles.overlayBlur} onClick={onClose}></div>

      {/* Modal */}
      <div className={styles.modal}>
        <div className={styles.modalContent}>
          <div className={styles.modalMain}>
            <h2></h2>
            <span className={styles.close} onClick={onClose}>&times;</span>
          </div>
          {/* 5.  onSubmit  handleSubmit */}
          <form onSubmit={handleSubmit}>
            <label htmlFor="tripName"> *</label>
            {/* 6.  value  onChange  State */}
            <input
              type="text"
              id="tripName"
              placeholder="..."
              value={tripName}
              onChange={(e) => setTripName(e.target.value)}
              required
            />

            <label htmlFor="tripText"> *</label>
            <textarea
              id="tripText"
              rows="3"
              placeholder="..."
              value={tripText}
              onChange={(e) => setTripText(e.target.value)}
              required
            />

            <label htmlFor="editTripRange"> *</label>
            {/* 7.  ID  input  */}
            <input
              id="editTripRange" // <-- ID 
              placeholder=" - "
              required
              readOnly //  flatpickr 
              style={{backgroundColor: "#fff"}} //  input 
            />

            <label htmlFor="tripBudget"></label>
            <input
              id="tripBudget"
              type="number" // <--  number
              step="any" // 
              placeholder="..."
              value={tripBudget}
              onChange={(e) => setTripBudget(e.target.value)}
            />

            <label htmlFor="tripPic"></label>
            <input
               type="file"
               id="tripPic"
               accept="image/*"
               onChange={handleImageChange} // <--  handleImageChange
            />
            {/*  preview  state */}
            {previewSrc && <img src={previewSrc} alt="Preview" style={{ maxWidth: "150px", padding: "10px", display: 'block' }} />}

            <div style={{ display: "flex", justifyContent: 'flex-end', marginTop: '20px' }}> {/*  */}
              <button type="submit" className={styles.btnSaveEdit}></button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}