import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import styles from "../css/tripManage.module.css";
import nav from "../css/main-nav.module.css";

export default function TripMainPage() {

  const handleDelete = async (roomId) => {
  // 2.  (Best Practice)
  if (!window.confirm('?')) {
    return; //  "Cancel" 
  }

  try {
    // 3.  API  Backend ( URL  /api/trips/:id)
    const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:3001';
    
    //  Endpoint  Backend ( 2)
    const response = await fetch(`${apiUrl}/trips_delete/${roomId}`, {
      method: 'DELETE',
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || '');
    }

    alert('!');
    window.location.reload();

  } catch (error) {
    console.error(':', error);
    alert(`: ${error.message}`);
  }
};

  const calculateTripDays = (startDate, endDate) => {
  // 1. 
  if (!startDate || !endDate) {
    return 0; //  '?' 
  }

  // 2.  string  Date objects
  const start = new Date(startDate);
  const end = new Date(endDate);

  // 3.  milliseconds
  const diffTime = end.getTime() - start.getTime();

  // 4.  milliseconds  
  // (1000ms * 60s * 60min * 24hr)
  const diffDays = Math.round(diffTime / (1000 * 60 * 60 * 24));

  // 5.  1 !
  // ( 29 .. - 30 ..  2 ,  1)
  return diffDays + 1; 
};
   const [trips, setTrips] = useState([]);
   const userData = JSON.parse(localStorage.getItem('userData'));

//  user_id
    const userId = userData?.user_id;
    const base_api = process.env.REACT_APP_API_URL;

  useEffect(() => {
    fetch(`${base_api}/trips?user_id=${userId}`)
      .then((res) => res.json())
      .then((data) => setTrips(data))
      .catch((err) => console.error(err));
  }, []);

  console.log(trips);
  const totalUsedBudget = trips.reduce((sum, trip) => {
    const budget = parseFloat(trip.total_budget) || 0;
    return sum + budget;
  }, 0);

  const totalTrips = trips.length;
  
    return (
      <>
        <header className={nav.header}>
          <div className={nav.container}>
            <div className={nav.logo}>
              <Link to="/tripMain">Travel Planner Pro </Link>
            </div>
            <nav className={nav.mainNav}>
              <ul>
                <li><Link to="/tripMain"></Link></li>
                <li><Link to="/tripManage"></Link></li>
                <li>
                  <img
                    src="https://travel-planner-profile-uploads-ab7bb12a.s3.us-east-1.amazonaws.com/istockphoto-1196083861-612x612.jpg"
                    alt="User Profile"
                    className={nav.profilePic}
                    onClick={() => (window.location.href = "/user")}
                  />
                </li>
              </ul>
            </nav>
          </div>
        </header>
  
        <main className={styles.tripMain}>
          <h1 className={styles.pageTitle}></h1>
  
          {/*  */}
          <div className={styles.tripSummary}>
            {/* <div className={styles.summaryCard}>
              <h3> </h3>
              <p>20 </p>
            </div> */}
            <div className={styles.summaryCard}>
              <h3> </h3>
              <p>{totalUsedBudget}</p>
            </div>
            <div className={styles.summaryCard}>
              <h3> </h3>
              <p>{totalTrips} </p>
            </div>
            {/* <div className={styles.summaryCard}>
              <h3> </h3>
              <p>18 </p>
            </div> */}
          </div>
  
          {/*  */}
          <div className={styles.tripList}>
            {trips.map((trip, index) => (
              <div key={index} className={styles.tripCard}>
                <div
                  className={styles.tripImage}
                  style={{ backgroundImage: `url(${trip.image})` }}
                />
                <div className={styles.tripContent}>
                  <div className={styles.tripTitle}>{trip.title}</div>
                  <div className={styles.tripDate}>{trip.description}</div>
                  <div className={styles.tripDate}> {calculateTripDays(trip.start_date, trip.end_date)} </div>
                  <div className={styles.tripMeta}>
                     {trip.total_budget.toLocaleString()} 
                  </div>
                  <div className={styles.tripActions}>
                    <button
                      className={`${styles.btn} ${styles.btnEdit}`}
                      onClick={() => window.location.href = `/tripPlan?room_id=${trip.room_id}`}
                    >
                      <i className="fas fa-edit"></i> 
                    </button>
                    <button className={`${styles.btn} ${styles.btnDelete}`} onClick={() => handleDelete(trip.room_id)}>
                      <i className="fas fa-trash"></i> 
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </main>
      </>
    );
  }



