import React, { useState } from "react";
import { Link } from "react-router-dom";
import { 
  CognitoIdentityProviderClient, 
  ForgotPasswordCommand,
  ConfirmForgotPasswordCommand 
} from "@aws-sdk/client-cognito-identity-provider";
import styles from '../css/ForgotPage.module.css';

const config = { region: process.env.REACT_APP_AWS_REGION  }

const cognitoClient = new CognitoIdentityProviderClient(config);
const clientId = process.env.REACT_APP_COGNITO_CLIENT_ID

function ForgotPage() {
  const [email, setEmail] = useState("");
  const [code, setCode] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [showVerification, setShowVerification] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [isSuccess, setIsSuccess] = useState(false);

  const handleError = (message) => {
    setIsSuccess(false);
    setAlertMessage(message);
    setShowAlert(true);
  };

  const handleSuccess = (message) => {
    setIsSuccess(true);
    setAlertMessage(message);
    setShowAlert(true);
  };

  const handleForgotPassword = async (e) => {
    e.preventDefault();
    try {
      const input = {
        ClientId: clientId,
        Username: email
      };

      const command = new ForgotPasswordCommand(input);
      const response = await cognitoClient.send(command);

      if (response.$metadata.httpStatusCode === 200) {
        setShowVerification(true);
        handleSuccess("");
      }
    } catch (error) {
      switch (error.name) {
        case 'UserNotFoundException':
          handleError("");
          break;
        case 'LimitExceededException':
          handleError(" ");
          break;
        default:
          handleError(" ");
      }
    }
  };

  const handleResetPassword = async (e) => {
    e.preventDefault();
    try {
      const input = {
        ClientId: clientId,
        Username: email,
        ConfirmationCode: code,
        Password: newPassword
      };

      const command = new ConfirmForgotPasswordCommand(input);
      const response = await cognitoClient.send(command);

      if (response.$metadata.httpStatusCode === 200) {
        handleSuccess(" ");
        setTimeout(() => {
          window.location.href = '/login';
        }, 2000);
      }
    } catch (error) {
      switch (error.name) {
        case 'CodeMismatchException':
          handleError("");
          break;
        case 'ExpiredCodeException':
          handleError(" ");
          break;
        default:
          handleError(" ");
      }
    }
  };

  return (
    <main className="forgot-page">
      <div className="forgot-card">
        <div className="forgot-h">
          <h2>?</h2>
          <p className="desc">
            {!showVerification 
              ? " "
              : " "
            }
          </p>
        </div>

        {!showVerification ? (
          <form className="forgot-form" onSubmit={handleForgotPassword}>
            <div className="form-group">
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder=""
                required
              />
            </div>
            <button type="submit" className="btn btn-primary">
              
            </button>
          </form>
        ) : (
          <form className="forgot-form" onSubmit={handleResetPassword}>
            <div className="form-group">
              <input
                type="text"
                value={code}
                onChange={(e) => setCode(e.target.value)}
                placeholder=""
                required
              />
            </div>
            <div className="form-group">
              <input
                type="password"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                placeholder=""
                required
              />
            </div>
            <button type="submit" className="btn btn-primary">
              
            </button>
          </form>
        )}

        <div className="signup-link">
          ? <Link to="/login"></Link>
        </div>
      </div>

      {showAlert && (
        <div className={styles.alertOverlay}>
          <div className={styles.alertBox}>
            <div className={styles.alertIcon}>
              {isSuccess ? '' : ''}
            </div>
            <h3 className={styles.alertTitle}>
              {isSuccess ? '' : ''}
            </h3>
            <p className={styles.alertMessage}>{alertMessage}</p>
            <button 
              className={styles.alertButton}
              onClick={() => setShowAlert(false)}
            >
              
            </button>
          </div>
        </div>
      )}
    </main>
  );
}

export default ForgotPage;
