import React, { useState, useEffect, useRef } from "react"; // <--  useRef
import { Link, useNavigate } from "react-router-dom";
import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.min.css";
import styles from "../css/tripMain.module.css"; // <-- CSS  ()
import nav from "../css/main-nav.module.css"; // <-- CSS  Navbar ()
import modalStyles from "../css/tripModal.module.css"; // <-- ***  CSS  Modal  ***
import { useAuth } from "../context/AuthContext";
// import axios from "axios"; //   tripService
import { tripService } from '../services/tripService'; // <-- Service 
import SearchBar from '../components/SearchBar';     // <-- Component 
import SortBar from '../components/SortBar';         // <-- Component 

// --- S3 Imports ( Upload ) ---
// import { S3Client, PutObjectCommand } from "@aws-sdk/client-s3";

export default function TripMainPage() {
  const navigate = useNavigate();
  const { isAuthenticated, user } = useAuth(); // <-- 

  // --- State  (, , ) ---
  const [trips, setTrips] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [filteredTrips, setFilteredTrips] = useState([]);
  const [sortBy, setSortBy] = useState('');
  const [sortOrder, setSortOrder] = useState('DESC');
  const [filters, setFilters] = useState({});
  const [isFilterVisible, setIsFilterVisible] = useState(true); //   isFilterVisible

  // --- State  (Modal ) ---
  const [showModal, setShowModal] = useState(false);
  const [preview, setPreview] = useState(null); // URL  preview 
  // State  Modal ( object )
  const [newTripData, setNewTripData] = useState({
    name: "",
    detail: "",
    start_date: "",
    end_date: "",
    budget: "",
    image: null //  URL  S3  Base64 
  });
  // State  File object ( S3 Upload)
  // const [selectedFile, setSelectedFile] = useState(null);
  // const [uploading, setUploading] = useState(false);
  // const fileInputRef = useRef();

  // --- Function / Modal () ---
  const openModal = () => {
    // Reset form data when opening
    setNewTripData({ name: "", detail: "", start_date: "", end_date: "", budget: "", image: null });
    setPreview(null);
    // setSelectedFile(null); // Reset file selection
    setShowModal(true);
  };
  const closeModal = () => {
    setShowModal(false);
    //  reset state   openModal 
  };

  // --- Function Preview  (, ) ---
  const previewImage = (event) => {
    const file = event.target.files[0];
    if (file) {
      setNewTripData({ ...newTripData, image: file });
      // setSelectedFile(file); // <--  File object ( S3)
      const reader = new FileReader();
      reader.onload = () => {
        setPreview(reader.result); //  preview
        //  S3   base64  ()
        // setNewTripData({ ...newTripData, image: reader.result });
      }
      reader.readAsDataURL(file);
    } else {
      // setSelectedFile(null);
      setPreview(null);
      // setNewTripData({ ...newTripData, image: null });
    }
  };

  // --- Function  (, ** Fetch **) ---
  // *** TODO:  tripService  Logic S3 Upload ***
  //   const createTrip = async (e) => {
  //     e.preventDefault(); // <--  e.preventDefault()  onSubmit
  //     // setUploading(true); // <--  S3
  //     const userData = JSON.parse(localStorage.getItem('userData'));

  // //  user_id
  //     const userId = userData?.user_id;
  //     // ---  S3 Upload () ---
  //     let imageUrlToSend = null; // <-- 
  //     // if (selectedFile) { ... upload to S3 ... imageUrlToSend = s3Url; }
  //     // else { ... }
  //     // --------------------------------

  //     try {
  //       //  State newTripData
  //       const dataToSend = {
  //         name: newTripData.name,
  //         detail: newTripData.detail,
  //         start_date: newTripData.start_date, //  flatpickr
  //         end_date: newTripData.end_date,   //  flatpickr
  //         budget: newTripData.budget === '' ? null : newTripData.budget, //  null 
  //         image: imageUrlToSend, // <--  URL  S3 ( null)
  //         user_id: userId
  //       };

  //       //  Base URL 
  //       const apiUrl = 'http://localhost:3001';

  //       const res = await fetch(`${apiUrl}/createplan`, { // <--  Endpoint 
  //         method: "POST",
  //         headers: { "Content-Type": "application/json" },
  //         body: JSON.stringify(dataToSend),
  //       });

  //       if (!res.ok) {
  //         const errorData = await res.json();
  //         throw new Error(errorData.message || 'Failed to create trip');
  //       }

  //       const data = await res.json();
  //       console.log(" Trip created:", data);
  //       alert("!");
  //       closeModal(); //  Modal
  //       fetchTrips(); // <--  :  fetchTrips !

  //     } catch (err) {
  //       console.error(" Error creating plan:", err);
  //       alert(`: ${err.message}`);
  //     } finally {
  //       // setUploading(false); // <--  S3
  //     }
  //   };

  const createTrip = async (e) => {
    e.preventDefault();
    const userData = JSON.parse(localStorage.getItem('userData'));
    const userId = userData?.user_id;

    // 1.  FormData object
    const formData = new FormData();

    // 2.  Text 
    formData.append('name', newTripData.name);
    formData.append('detail', newTripData.detail);
    formData.append('start_date', newTripData.start_date);
    formData.append('end_date', newTripData.end_date);
    formData.append('budget', newTripData.budget === '' ? null : newTripData.budget);
    formData.append('user_id', userId);

    // 3.  File object  ()
    // Backend  key 'image'
    if (newTripData.image) {
      formData.append('image', newTripData.image);
    }

    // setUploading(true); // <-- 

    try {
      const apiUrl = process.env.REACT_APP_API_URL;

      const res = await fetch(`${apiUrl}/createplan`, {
        method: "POST",
        // 4.  headers: { "Content-Type": "application/json" } 
        //    Browser  Content-Type  'multipart/form-data' 

        // 5.  formData  body
        body: formData,
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || 'Failed to create trip');
      }

      const data = await res.json();
      console.log(" Trip created:", data);
      alert("!");
      closeModal();
      fetchTrips();

    } catch (err) {
      console.error(" Error creating plan:", err);
      alert(`: ${err.message}`);
    } finally {
      // setUploading(false); 
    }
  };



  // --- Function  (, ) ---
  const fetchTrips = async (sort = sortBy, order = sortOrder, currentFilters = filters, query = searchQuery) => {
    setLoading(true); // <--  Loading  fetch
    setError(null);   // <--  Error 
    try {
      //  tripService 
      const response = await tripService.getPublicTrips({
        sortBy: sort,
        order,
        ...currentFilters,
        search: query //  query parameter  'search' ( Backend )
      });
      if (response.status === 'success') {
        setTrips(response.data); // 
        setFilteredTrips(response.data); //  ( logic filter )
      } else {
        throw new Error(response.message || 'Failed to fetch trips');
      }
    } catch (error) {
      console.error('Error fetching trips:', error);
      setError(': ' + error.message);
      setTrips([]); //  Error
      setFilteredTrips([]);
    } finally {
      setLoading(false); // <--  Loading 
    }
  };

  // --- useEffect  (, ) ---
  // useEffect(() => {
  //   if (!isAuthenticated) {
  //     navigate('/login'); // Redirect  Login
  //     return; // 
  //   }
  //   fetchTrips(); //  fetch  Component 
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [isAuthenticated, navigate]); //  Login 

  // --- Handlers  Search, Sort, Filter () ---
  const handleSearch = (query) => {
    setSearchQuery(query);
    //  debounce search  trigger fetch 
    fetchTrips(sortBy, sortOrder, filters, query); //  fetch  search
  };

  const handleSort = (value) => {
    setSortBy(value);
    fetchTrips(value, sortOrder, filters, searchQuery); //  fetch 
  };

  const handleOrderChange = (value) => {
    setSortOrder(value);
    fetchTrips(sortBy, value, filters, searchQuery); //  fetch 
  };

  const handleFilter = (key, value) => {
    const newFilters = { ...filters, [key]: value };
    setFilters(newFilters);
    fetchTrips(sortBy, sortOrder, newFilters, searchQuery); //  fetch 
  };

  // --- Function Format  () ---
  const formatDate = (dateString) => {
    if (!dateString) return "-";
    const options = { year: "numeric", month: "long", day: "numeric" };
    try {
      return new Date(dateString).toLocaleDateString("th-TH", options);
    } catch (e) {
      return dateString; // Return original if invalid
    }
  };

  // --- useEffect  Flatpickr ** Modal** (, ) ---
  useEffect(() => {
    flatpickr("#tripRange", {
      mode: "range",
      dateFormat: "Y-m-d",
      minDate: "today",
      locale: "th",
      onClose: function (selectedDates, dateStr, instance) {
        if (selectedDates.length === 2) {
          const start = selectedDates[0];
          const end = selectedDates[1];

          //  ISO date  backend
          setNewTripData(prevState => ({
            ...prevState, // <--  React " State !"
            start_date: start.toISOString().split("T")[0], // "YYYY-MM-DD"
            end_date: end.toISOString().split("T")[0],   // "YYYY-MM-DD"
          }));

          //  display 
          const monthsTH = [
            "", "", "", "", "", "",
            "", "", "", "", "", ""
          ];

          const dayStart = start.getDate();
          const monthStart = monthsTH[start.getMonth()];
          const yearStart = start.getFullYear() + 543;

          const dayEnd = end.getDate();
          const monthEnd = monthsTH[end.getMonth()];
          const yearEnd = end.getFullYear() + 543;

          let displayText = "";
          if (monthStart === monthEnd && yearStart === yearEnd) {
            displayText = `${dayStart} - ${dayEnd} ${monthStart} ${yearStart}`;
          } else {
            displayText = `${dayStart} ${monthStart} ${yearStart} - ${dayEnd} ${monthEnd} ${yearEnd}`;
          }

          instance.input.value = displayText;
        }
      }
    });
  }, [showModal]); // <--  Effect  showModal 

  // --- JSX ---
  return (
    <>
      <header className={nav.header}>
        <div className={nav.container}>
          <div className={nav.logo}>
            <Link to="/tripMain">Travel Planner Pro </Link>
          </div>
          <nav className={nav.mainNav}>
            <ul>
              <li><Link to="/tripMain"></Link></li>
              <li><Link to="/tripManage"></Link></li>
              <li><button className={nav.btnback} onClick={openModal}></button></li>
              <li>
                <img
                  src="https://travel-planner-profile-uploads-ab7bb12a.s3.us-east-1.amazonaws.com/istockphoto-1196083861-612x612.jpg"
                  alt="User Profile"
                  className={nav.profilePic}
                  onClick={() => window.location.href = "/user"}
                />
              </li>
            </ul>
          </nav>
        </div>
      </header>

      {/* Main Content () */}
      <main className={styles.dashboardMain}>
        <div className={styles.container}>
          {/* Header  Section () */}
          <header className={styles.header}>
            <h2></h2>
            {/*   Navbar */}
          </header>

          {/* Search & Sort Bars () */}
          <SearchBar onSearch={handleSearch} />
          <SortBar
            onSort={handleSort}
            onOrderChange={handleOrderChange}
            onFilter={handleFilter}
            isVisible={isFilterVisible}
            onToggleVisibility={() => setIsFilterVisible(!isFilterVisible)}
          />

          {/* Trip Grid Section */}
          <section className={styles.tripsSection}>
            {loading && <div className={styles.loading}>...</div>}
            {error && <div className={styles.error}>{error}</div>}

            {/*  Grid  Error */}
            {!loading && !error && (
              <div className={styles.tripsGrid}>
                {filteredTrips.length === 0 && <p></p>}
                {filteredTrips.map((trip) => (
                  //  Card  (TripMainPage2.jsx)
                  <div
                    key={trip.room_id}
                    className={styles.tripCard}
                    //  Background 
                    style={{ backgroundImage: `url(${trip.image || 'default-image.jpg'})` }}
                    onClick={() => navigate(`/tripPlanInfo?room_id=${trip.room_id}`)} // <--  Info 
                  >
                    <div className={styles.tripContent}>
                      <div className={styles.tripHeader}>
                        <h2>{trip.title}</h2>
                        <span className={styles.dates}>
                          {formatDate(trip.start_date)} - {formatDate(trip.end_date)}
                        </span>
                      </div>
                      {/*  Description */}
                      {/* <p className={styles.description}>{trip.description}</p> */}
                      {/*  Owner ( API ) */}
                      {/* <div className={styles.ownerInfo}> ... </div> */}
                      {/*  Budget ( API ) */}
                      {/* <div className={styles.tripFooter}> ... </div> */}
                      {/*  ( Link  Action) */}
                      <div className={styles.tripActions}>
                        <button className={`${styles.btn} ${styles.btnSecondary}`}></button>
                        {/*  Copy  Logic  */}
                        {/* <button className={`${styles.btn} ${styles.btnCopy}`}></button> */}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </section>
        </div>
      </main>

      {/* Modal  (,  CSS ) */}
      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <div className="modal-main">
              <h2></h2>
              <span className="close" onClick={closeModal}>&times;</span>
            </div>

            <form>
              <label> *</label>
              <input type="text" placeholder="..." value={newTripData.name} onChange={(e) => setNewTripData({ ...newTripData, name: e.target.value })} required />

              <label> *</label>
              <textarea rows="3" placeholder="..." value={newTripData.detail} onChange={(e) => setNewTripData({ ...newTripData, detail: e.target.value })} required />

              <label> *</label>
              <input id="tripRange" placeholder=" - " required />
              <label></label>
              <input placeholder="..." value={newTripData.budget} onChange={(e) => setNewTripData({ ...newTripData, budget: e.target.value })} />

              <label></label>
              <input type="file" accept="image/*" onChange={previewImage} />

                <div>
                  {preview && <img src={preview} alt="preview" className={styles.previewImg} />}

                </div>
                <button type="submit" className="btn-save-edit" onClick={createTrip}></button>

            </form>
          </div>
        </div>
      )}
    </>
  );
}