
import React, { useState, useEffect } from "react";
import styles from "../css/ActivityTripModal.module.css";

const API_URL = process.env.REACT_APP_API_URL;

export default function ActivityModal({
  type = "add",
  open,
  onClose,
  editData = null,
  date = null,
  room_id = null
}) {
  const isEdit = type === "edit";

  // 1.  state 
  const [activities, setActivities] = useState([{ text: "" }]);

  // 2.   useEffect  
  useEffect(() => {
    // 3.  Modal 
    if (open) {
      if (isEdit && editData && editData.ItineraryDetails) {
        // 4.  "" 
        //  { description: "..." }  { text: "..." }
        const detailsFromData = editData.ItineraryDetails.map(detail => ({
          text: detail.description
        }));

        if (detailsFromData.length > 0) {
          setActivities(detailsFromData);
        } else {
          setActivities([{ text: "" }]); //   details
        }
        
      } else {
        // 5.  "" 
        setActivities([{ text: "" }]);
      }
    }
  }, [open, isEdit, editData]); // 6.  Effect  Modal /

  
  // ( 3 )
  const handleAddDetail = () => {
    setActivities([...activities, { text: "" }]);
  };

  const handleDeleteDetail = (index) => {
    setActivities(activities.filter((_, i) => i !== index));
  };

  const handleDetailChange = (index, value) => {
    const newActivities = [...activities];
    newActivities[index].text = value;
    setActivities(newActivities);
  };

  // (handleSubmit  Add/Edit )
  const handleSubmit = async (e) => { 
    e.preventDefault();

    const data = {
      main: e.target.activityMain.value,
      time: e.target.activityTime.value,
      location: e.target.activityLocation.value,
      locationLink: e.target.activityLocationLink.value,
      details: activities,
      date: isEdit ? editData.date : date, // <-- 
      room_id: room_id
    };

    if (isEdit) {
      // ########## LOGIC  (EDIT) ##########
      if (!editData || !editData.itinerary_id) {
        alert(":  ID ");
        return;
      }
      try {
        const response = await fetch(`${API_URL}/editActivity/${editData.itinerary_id}`, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(data),
        });
        if (!response.ok) throw new Error('Failed to edit activity');
        alert("!");
        onClose();
        window.location.reload(); 
      } catch (err) {
        alert(`: ${err.message}`);
      }

    } else {
      // ########## LOGIC  (ADD) ##########
      try {
        const response = await fetch(`${API_URL}/addActivity`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(data),
        });
        if (!response.ok) throw new Error('Failed to add activity');
        alert("!");
        onClose();
        window.location.reload(); 
      } catch (err) {
        alert(`: ${err.message}`);
      }
    }
  };

  if (!open) return null;

  // ( return ...)

  return (
    <>
      {/* Blur background */}
      <div className={styles.overlayBlur} onClick={onClose}></div>

      {/* Modal */}
      <div className={styles.modal}>
        <div className={styles.modalContent}>
          <div className={styles.modalMain}>
            <h2>
              {isEdit
                ? ` ${editData?.date || "1 .."}`
                : " 1 .."}
            </h2>
            <span className={styles.close} onClick={onClose}>
              &times;
            </span>
          </div>
          <form onSubmit={handleSubmit}>
            <label> *</label>
            <input
              name="activityMain"
              type="text"
              placeholder=""
              defaultValue={editData?.title || ""}
              required
            />

            <label> *</label>
            <input
              name="activityTime"
              type="time"
              defaultValue={editData?.time || ""}
              required
            />

            <label> *</label>
            <input
              name="activityLocation"
              placeholder=""
              defaultValue={editData?.location || ""}
              required
            />

            <label></label>
            <input
              name="activityLocationLink"
              placeholder=""
              defaultValue={editData?.map || ""}
            />

            <label></label>
            <div
              className={
                isEdit
                  ? styles.editactivityDetails
                  : styles.activityDetails
              }
            >
              {activities.map((activity, index) => (
                <div
                  className={
                    isEdit
                      ? styles.editactivityDeatilsInput
                      : styles.activityDeatilsInput
                  }
                  style={{
                    marginTop: "10px",
                  }}
                  key={index}
                >
                  <input
                    placeholder=""
                    value={activity.text}
                    onChange={(e) =>
                      handleDetailChange(index, e.target.value)
                    }
                    style={{
                      width: "90%",
                    }}
                  />
                  <button
                    type="button"
                    className={styles.btnDelete}
                    
                    onClick={() => handleDeleteDetail(index)}
                  >
                    <i className="fa-solid fa-trash"

                    ></i>
                  </button>
                </div>
              ))}
            </div>

            <div style={{ display: "flex" }}>
              <div style={{ marginLeft: "auto", padding: "10px", marginTop: "10px" , gap: "20px" ,}}>
                <button
                  type="button"
                  className={styles.btnAddDetails}
                  onClick={handleAddDetail}
                >
                  
                </button>
                <button type="submit" className={styles.btnSaveEdit}>
                  
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}
